.stars {
  margin-top: 16px;
  padding-left: 16px;
  display: flex;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  list-style: none;
  display: flex;
}

.stars--1 .star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  display: flex;
}

.container {
  grid-template-columns: repeat(4, 200px);
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  main {
    grid-template-columns: repeat(4, 200px);
  }
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

* {
  box-sizing: border-box;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__link {
  display: inline-block;
}

.nav {
  justify-content: space-between;
  height: 60px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 3px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active, :hover {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  transition: transform .3s smooth;
  border: 1px solid #f5f5f5;
  border-radius: 5px;
  width: 200px;
  height: 408px;
}

.card:hover {
  transform: scale(1.2);
}

.card__list {
  list-style: none;
}

.card__photo {
  width: 160px;
  height: 206px;
  margin: 0 19px;
  padding-top: 32px;
  padding-bottom: 40px;
  display: flex;
}

.card__title {
  text-align: left;
  color: #060b35;
  margin: 0;
